.product {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s ease-in-out;
}

.product__photo {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.product__title:hover {
  color: #34568b;
}

.product__text-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product__review-container {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.product__review-count {
  color: #060b35;
}

.product__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product__text-price {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product__text-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product:hover {
  transform: scale(1.2);
}

.button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.button:hover {
  color: #00acdc;
  background-color: #f3f3f3;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.main {
  grid-gap: 46px 48px;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  padding: 50px 40px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.header {
  color: #00acdc;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  color: #060b35;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  white-space: nowrap;
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

* {
  box-sizing: border-box;
  cursor: pointer;
}

body {
  margin: 0;
}

p {
  margin: 0;
  padding: 0;
}
